.skills-section {
  background-color: #121212; /* 검정색 박스 */
  padding: 60px 20px;
  color: white;
  text-align: center;
  max-width: 1200px;
  margin: 20px auto;
  border-radius: 10px; /* 박스 모서리 둥글게 */
}

.skills-section h2 {
  font-size: 2.5rem;
  color: #0073e6;
  margin-bottom: 20px;
}

.skills-list {
  display: flex;
  justify-content: center; /* 좌우 대칭을 위한 중앙 정렬 */
  gap: 15px; /* 항목 간 간격 */
  margin-bottom: 20px;
}

.skill-item {
  background-color: #1e1e1e; /* 항목 박스 색상 */
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1.2rem;
  min-width: 100px; /* 항목 크기 */
  text-align: center;
}

