.header {
  background-color: #121212; /* 검정색 박스 */
  color: white;
  padding: 100px 20px;
  text-align: center; /* 중앙 정렬 */
  max-width: 1200px;
  margin: 20px auto; /* 중앙 배치 */
  border-radius: 10px; /* 박스 모서리 둥글게 */
}

.header-content h1 {
  font-size: 4rem;
  color: #0073e6;
}

.header-content p {
  font-size: 1.5rem;
  color: #f0f0f0;
}

.profile-photo {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-top: 20px;
}

.contact-btn {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: #0073e6;
  color: white;
  border-radius: 5px;
  text-decoration: none;
}

