.about-section {
  background-color: #121212; /* 검정색 박스 */
  padding: 60px 20px;
  color: white;
  text-align: center; /* 중앙 정렬 */
  max-width: 1200px;
  margin: 20px auto; /* 중앙 배치 */
  border-radius: 10px; /* 박스 모서리 둥글게 */
}

.about-section h2 {
  font-size: 2.5rem;
  color: #0073e6;
  margin-bottom: 20px;
}

.about-section p {
  font-size: 1.2rem; /* 본문 폰트 크기 줄임 */
  color: #f0f0f0;
  max-width: 800px;
  margin: 0 auto; /* 중앙 정렬 */
}

