.projects-section {
  background-color: #121212; /* 검정색 박스 */
  padding: 60px 20px;
  color: white;
  text-align: center;
  max-width: 1200px;
  margin: 20px auto;
  border-radius: 10px; /* 박스 모서리 둥글게 */
}

.projects-section h2 {
  font-size: 2.5rem;
  color: #0073e6;
  margin-bottom: 40px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* 좌우 대칭을 위한 중앙 정렬 */
  gap: 20px;
}

.project-card {
  background-color: #1e1e1e; /* 항목 박스 */
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
  border: 1px solid #0073e6;
}

.project-card h3 {
  color: #0073e6;
  font-size: 1.5rem;
}

.project-card p {
  color: #f0f0f0;
  font-size: 1rem;
}

