.contact-section {
  background-color: #121212; /* 검정색 박스 */
  padding: 60px 20px;
  color: white;
  text-align: center;
  max-width: 1200px;
  margin: 20px auto;
  border-radius: 10px; /* 박스 모서리 둥글게 */
}

.contact-section h2 {
  font-size: 2.5rem;
  color: #0073e6;
  margin-bottom: 20px;
}

.contact-info p {
  font-size: 1.2rem;
  color: #f0f0f0;
}

.contact-info a {
  color: #0073e6;
  text-decoration: none;
}

